import React, { Component } from 'react';
import PageContainer from '../../components/PageContainer';

import DataPage001 from './Page001';
import DataPage002 from './Page002';
import DataPage003 from './Page003';
import DataPage004 from './Page004';
import DataPage005 from './Page005';
import DataPage006 from './Page006';
import DataPage007 from './Page007';

const pages = [
  { page: DataPage001, title: '探索的データ分析', },
  { page: DataPage002, title: 'データと標本の分布', },
  { page: DataPage003, title: '統計実験と有意性検定', },
  { page: DataPage004, title: '回帰と予測', },
  { page: DataPage005, title: '分類', },
  { page: DataPage006, title: '統計的機械学習', },
  { page: DataPage007, title: '教師なし学習', },
]

export default class DataScience extends Component {
  render() {
    return (
      <div>
        {
          pages.map( (v, i) => {
            return v.page({
              pageInfo: {
                title: v.title,
                page: i+1,
                maxPage: pages.length,
              },
            })
          })
        }
      </div>
    )
  }
}
